import React from "react";
import "./statushealth.scss";
import {IHealth} from '../../allInterfaces'


export const StatusHealth: React.FC<IHealth> = ({ status = "UNDEFINED" }) => {
  return (
    <div className="m-status">
      <div className={`m-status__line${status === "HAPPY" ? " active" : ""}`}>
        😊
      </div>
      <div className={`m-status__line${status === "NORMAL" ? " active" : ""}`}>
        😐
      </div>
      <div className={`m-status__line${status === "UNHAPPY" ? " active" : ""}`}>
        🙁
      </div>
    </div>
  );
};
